import * as PIXI from "pixi.js-legacy";
import gsap from "gsap";
import keyboard from "../logic/Keyboard";
import Util from "../logic/Util";

export default class Triangle extends PIXI.Graphics {
    constructor(x, y, boardSize, cellSize) {
        super();
        this.x = x;
        this.y = y;
        this.boardSize = boardSize;
        this.cellSize = cellSize;
        this.allowMove = true;
        this.animateSpeed = 0.4;

        // x1,y1,x2,y2,x3,y3
        // change triangle size in this array
        this.coords = [0, 0, 0, 2, 2, 2];

        let left = keyboard("ArrowLeft"),
            up = keyboard("ArrowUp"),
            right = keyboard("ArrowRight"),
            down = keyboard("ArrowDown"),
            q = keyboard("q"),
            e = keyboard("e"),
            h = keyboard("h"),
            v = keyboard("v");

        q.press = () => {
            if (!this.allowMove) return;
            this.rotate(90, this.coords[0], this.coords[1], this.coords);
        };
        e.press = () => {
            if (!this.allowMove) return;
            this.rotate(-90, this.coords[0], this.coords[1], this.coords);
        };
        left.press = () => {
            this.translate(-1, 0);
        };
        right.press = () => {
            this.translate(1, 0);
        };
        up.press = () => {
            this.translate(0, -1);
        };
        down.press = () => {
            this.translate(0, 1);
        };
        h.press = () => {
            this.reflect(true);
        };
        v.press = () => {
            this.reflect(false);
        };
    }
    onMoveComplete() {}
    translate(dx, dy, animate = true) {
        if (!this.allowMove) return;

        if (!animate) {
            for (let i = 0; i < this.coords.length; i++) {
                this.coords[i] += i % 2 === 1 ? dy : dx;
            }
            return;
        }

        this.allowMove = false;
        gsap.to(this.coords, {
            0: this.coords[0] + dx,
            1: this.coords[1] + dy,
            2: this.coords[2] + dx,
            3: this.coords[3] + dy,
            4: this.coords[4] + dx,
            5: this.coords[5] + dy,
            duration: this.animateSpeed,
            ease: "bounce.out",
            onComplete: () => {
                this.allowMove = true;
                console.log(Util.worldToLocal(this.coords));
            }
        });
    }
    rotate(angle, originX, originY, coords) {
        if (!this.allowMove) return;
        this.allowMove = false;

        angle *= Math.PI / 180;
        const angleSin = Math.sin(angle);
        const angleCos = Math.cos(angle);

        let copy = coords.concat();
        let newCoords = [];
        for (let i = 0; i < coords.length; i += 2) {
            newCoords[i] = Math.round(
                (copy[i] - originX) * angleCos +
                    (copy[i + 1] - originY) * angleSin +
                    originX
            );
            newCoords[i + 1] = Math.round(
                (copy[i + 1] - originY) * angleCos -
                    (copy[i] - originX) * angleSin +
                    originY
            );
        }
        gsap.to(this.coords, {
            0: newCoords[0],
            1: newCoords[1],
            2: newCoords[2],
            3: newCoords[3],
            4: newCoords[4],
            5: newCoords[5],
            duration: this.animateSpeed,
            ease: "bounce.out",
            onComplete: () => {
                this.allowMove = true;
                // console.log(Util.worldToLocal(this.coords));
            }
        });
    }
    reflect(horizontal, animate = true) {
        if (!this.allowMove) return;
        this.allowMove = false;
        if (!animate) {
            return;
        }
        if (horizontal) {
            gsap.to(this.coords, {
                0: this.coords[0] * -1,
                2: this.coords[2] * -1,
                4: this.coords[4] * -1,
                duration: this.animateSpeed,
                ease: "bounce.out",
                onComplete: () => {
                    this.allowMove = true;
                    // console.log(Util.worldToLocal(this.coords));
                }
            });
        } else {
            gsap.to(this.coords, {
                1: this.coords[1] * -1,
                3: this.coords[3] * -1,
                5: this.coords[5] * -1,
                duration: this.animateSpeed,
                ease: "bounce.out",
                onComplete: () => {
                    this.allowMove = true;
                }
            });
        }
    }
    draw() {
        let c = Util.localToWorld(this.coords);
        const size = this.cellSize;
        this.clear();
        this.beginFill(0xff0000);
        this.lineStyle(2);
        this.moveTo(c[0] * size, c[1] * size);
        this.lineTo(c[2] * size, c[3] * size);
        this.lineTo(c[4] * size, c[5] * size);
        this.closePath();
        this.endFill();
    }
}
