import * as PIXI from "pixi.js-legacy";
import Triangle from "./Triangle";
import Transition from "./Transition";
import keyboard from "../logic/Keyboard";
import Grid from "./Grid";
import GridNumbers from "./GridNumbers";
import Goal from "./Goal";
import Util from "../logic/Util";
export default class Game {
    constructor(app, props) {
        this.app = app;
        this.entities = [];
        this.tri = undefined;
        this.goal = undefined;
        this.allowMove = true;
    }
    start() {
        const root = new PIXI.Container();
        const container = new PIXI.Container();
        root.addChild(container);
        this.app.stage.addChild(root);

        // this.app.stage.addChild(container);
        let totalGridSize = 500;
        let cellSize = totalGridSize / 20;
        let grid = new Grid(16, 16, totalGridSize, totalGridSize);

        // container.setTransform(1024 / 2, 572, 0, -1);
        // root.y += 400;
        // container.worldTransform.y += 500;
        // container.updateTransform();
        // console.log(container.y);

        container.addChild(grid);
        let gridNums = new GridNumbers(
            totalGridSize / 2,
            totalGridSize / 2,
            cellSize
        );

        let p = keyboard("p");
        p.press = () => {
            console.log(this.tri.coords, this.goal.coords);
            console.log(Util.checkWin(this.tri.coords, this.goal.coords));
        };

        this.tri = new Triangle(16, 16, totalGridSize, cellSize);
        container.addChild(gridNums);

        this.goal = new Goal([3, 3, 3, 5, 5, 5], cellSize);
        container.addChild(this.goal);
        container.addChild(this.tri);

        this.app.stage.addChild(new Transition(1024, 576));

        // Move container to the center
        container.x = this.app.screen.width / 3;
        container.y = this.app.screen.height / 2;

        // Center bunny sprite in local container coordinates
        container.pivot.x = container.width / 2;
        container.pivot.y = container.height / 2;

        console.log(
            `Point is in the triangle:${Util.pointInTri(
                this.tri.coords,
                2,
                -2
            )}`
        );

        // Listen for animate update
        this.app.ticker.add(delta => {
            // use delta to create frame-independent transform
            // container.rotation -= 0.005 * delta;
            if (this.tri) {
                this.tri.draw();
            }
        });
    }
    acceptCommand(command) {
        console.log(command);
    }
    onMoveComplete() {
        for (let i in this.entities) {
            if (Util.pointInTri(this.tri.coords, i.x, i.y)) {
            }
        }
        if (Util.checkWin(this.tri.coords, this.goal.coords)) {
            this.allowMove = false;
            this.levelComplete();
        } else {
            this.allowMove = true;
        }
    }
    levelComplete() {
        console.log("You win!");
    }
}
