import React, { Component } from "react";
import * as PIXI from "pixi.js-legacy";
import TestMenu from "./components/ui/TestMenu";
import Game from "./pixi/Game";
import "./App.css";

class App extends Component {
    constructor(props) {
        super(props);
        this.canvasRef = React.createRef();
        this.canvas = this.canvasRef.current;
        this.app = undefined;
        this.game = undefined;
        this.state = {};
    }
    componentDidMount() {
        setTimeout(() => {
            this.canvas = this.canvasRef.current;
            this.app = new PIXI.Application({
                width: 1024,
                height: 576,
                backgroundColor: 0x1099bb,
                view: this.canvas,
                resolution: window.devicePixelRatio || 1
            });
            this.game = new Game(this.app);
            this.game.start();
        }, 5);
    }
    moveUpTest = () => {
        this.game.acceptCommand("tried to move up");
    };
    render() {
        return (
            <div id="app-container">
                <h3>Temporary Controls</h3>
                <p>Use the arrow keys to translate</p>
                <p>Use Q and E to rotate</p>
                <p>Use H and V to reflect</p>
                <div id="game-container">
                    <canvas ref={this.canvasRef} />
                    <TestMenu
                        hidden={false}
                        moveUp={this.moveUpTest}
                        moveDown={() => {}}
                        moveLeft={() => {}}
                        moveRight={() => {}}
                    />
                </div>
            </div>
        );
    }
}

export default App;
